.card-container-border{
    margin: auto;
    background-color: white;
    border-radius: .5rem;
    height:100%;
    width: 100%;
}

.card-whole-page{
    height: 100%;
}

.card-container-no-border{
    border-radius: .5rem;
    width: 100%;
    
}

@media (max-width: 600px){
    .card-body{
        margin: 16px;
    }
}

.card-title{
    font-size: 1.5rem;
    text-align: center;
}

.card-body{
    text-align: left;
    height:100%;
    padding:0;
}

.underline-card-title{
    height: 4px;
}